import React from "react";
import * as yup from "yup";
import { connect } from "react-redux";
import { register, login } from "../../Actions/User";
import Swal from "sweetalert2";
import api from "../../Api/user";
import { withSnackbar } from "notistack";
import TextField from "@material-ui/core/TextField";
import { Formik, Field, Form } from "formik";
import { Button } from "@material-ui/core";

import "./style.scss";

const checkUsername = value => {
  return api
    .get(`TimKiemNguoiDung?MaNhom=GP09&tuKhoa=${value}`)
    .then(result => {
      const taiKhoan = result.data;
      let flag = true;
      taiKhoan.forEach(item => {
        if (item.taiKhoan.toUpperCase() === value.toUpperCase()) {
          flag = false;
        }
      });
      return flag;
    });
};

const validationSchema = yup.object().shape({
  hoTen: yup.string().required("Vui lng khng  trng"),
  taiKhoan: yup
    .string()
    .required("Vui lng khng  trng")
    .min(8, " di ti thiu 8 k t")
    .max(32, " di ti a 32 k t")
    .test("check taiKhoan", "Ti khon  tn ti", value => {
      return checkUsername(value);
    }),
  matKhau: yup
    .string()
    .required("Vui lng khng  trng")
    .min(8, " di ti thiu 8 k t")
    .max(32, " di ti a 32 k t"),
  email: yup
    .string()
    .email("Vui lng nhp ng nh dng")
    .matches(
      /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
      "Vui lng nhp ng nh dng1"
    )
    .required("Vui lng khng  trng"),
  soDt: yup
    .number()
    .required("Vui lng khng  trng")
    .typeError("Vui lng ch nhp s")
});

const FormRegister = ({ enqueueSnackbar, ...props }) => {
  return (
    <Formik
      initialValues={{
        hoTen: "",
        taiKhoan: "",
        matKhau: "",
        email: "",
        soDt: ""
      }}
      onSubmit={(values, { setFieldError }) => {
        const newUser = {
          ...values,
          maNhom: "GP09",
          maLoaiNguoiDung: "KhachHang"
        };
        register(newUser)
          .then(() => {
            props.handleClose();
          })
          .then(result => {
            Swal.fire({
              icon: "success",
              title: "ng k thnh cng",
              showCancelButton: true,
              cancelButtonColor: "#d33",
              confirmButtonText: "ng nhp",
              cancelButtonText: "khng",
              reverseButtons: true
            }).then(result => {
              result.value &&
                api
                  .post("DangNhap", {
                    taiKhoan: newUser.taiKhoan,
                    matKhau: newUser.matKhau
                  })
                  .then(result => {
                    props.login(result.data);
                    enqueueSnackbar(`Xin cho ${result.data.hoTen}`, {
                      variant: "success",
                      anchorOrigin: {
                        vertical: "top",
                        horizontal: "right"
                      }
                    });
                  });
            });
          })
          .catch(err => setFieldError("email", err.response.message));
      }}
      validationSchema={validationSchema}
    >
      {({ handleBlur, handleChange, handleSubmit }) => {
        return (
          <Form onSubmit={handleSubmit} className="form-register">
            <Field>
              {({ form }) => {
                return (
                  <TextField
                    error={form.touched.hoTen && !!form.errors.hoTen}
                    name="hoTen"
                    label="H v tn"
                    margin="normal"
                    variant="outlined"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    helperText={
                      form.touched.hoTen && form.errors.hoTen
                        ? form.errors.hoTen
                        : ""
                    }
                  />
                );
              }}
            </Field>
            <Field>
              {({ form }) => {
                return (
                  <TextField
                    name="taiKhoan"
                    label="Ti khon"
                    margin="normal"
                    variant="outlined"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={form.touched.taiKhoan && !!form.errors.taiKhoan}
                    helperText={
                      form.touched.taiKhoan && form.errors.taiKhoan
                        ? form.errors.taiKhoan
                        : ""
                    }
                  />
                );
              }}
            </Field>
            <Field>
              {({ form }) => {
                return (
                  <TextField
                    name="matKhau"
                    label="Mt khu"
                    margin="normal"
                    variant="outlined"
                    type="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={form.touched.matKhau && !!form.errors.matKhau}
                    helperText={
                      form.touched.matKhau && form.errors.matKhau
                        ? form.errors.matKhau
                        : ""
                    }
                  />
                );
              }}
            </Field>
            <Field>
              {({ form }) => {
                return (
                  <TextField
                    name="email"
                    label="Email"
                    margin="normal"
                    variant="outlined"
                    type="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={form.touched.email && !!form.errors.email}
                    helperText={
                      form.touched.email && form.errors.email
                        ? form.errors.email
                        : ""
                    }
                  />
                );
              }}
            </Field>
            <Field>
              {({ form }) => {
                return (
                  <TextField
                    name="soDt"
                    label="S in thoi"
                    margin="normal"
                    variant="outlined"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={form.touched.soDt && !!form.errors.soDt}
                    helperText={
                      form.touched.soDt && form.errors.soDt
                        ? form.errors.soDt
                        : ""
                    }
                  />
                );
              }}
            </Field>
            <div className="btn-group">
              <Button variant="contained" onClick={() => props.setRegister()}>
                Quay v
              </Button>
              <Button variant="contained" type="submit" onClick={handleSubmit}>
                ng k
              </Button>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
};
const mapDispatchToProps = dispatch => {
  return {
    register: user => {
      register(user);
    },
    login: user => {
      dispatch(login(user));
    }
  };
};
export default connect(null, mapDispatchToProps)(withSnackbar(FormRegister));
