import React from "react";
import "./style.scss";
import userDefault from "../../Assets/img/user-default.png";
import likeImg from "../../Assets/img/like.png";

import Rating from "@material-ui/lab/Rating";
import StarBorderIcon from "@material-ui/icons/StarBorder";

const RateOfFilmDetail = () => {
  return (
    <div className="rate-of-tab">
      <div className="wrap-content">
        <div className="input-review">
          <img src={userDefault} alt="..." />
          <span>Bn ngh g v phim ny?</span>
          <Rating
            name="customized-empty"
            value={5}
            readOnly
            emptyIcon={<StarBorderIcon fontSize="inherit" />}
          />
        </div>
        <div className="list-comment">
          <div className="item-comment">
            <div className="main-info">
              <div className="info-reviewer">
                <img src={userDefault} alt="..." />
                <div className="info-reviewer-name">
                  <div>V Anh Dng</div>
                  <p className="info-reviewer-time">20 pht trc</p>
                </div>
              </div>
              <div className="info-start">
                <p>10</p>
                <Rating readOnly value={2} />
              </div>
            </div>
            <div className="content-comment">Phim hay</div>
          </div>
          <div className="like">
            <img src={likeImg} alt="..." />
            <span className="likes">0 thch</span>
          </div>
        </div>
        <div className="list-comment">
          <div className="item-comment">
            <div className="main-info">
              <div className="info-reviewer">
                <img src={userDefault} alt="..." />
                <div className="info-reviewer-name">
                  <div>Trn Thi Nhn</div>
                  <p className="info-reviewer-time">20 pht trc</p>
                </div>
              </div>
              <div className="info-start">
                <p>10</p>
                <Rating readOnly value={2} />
              </div>
            </div>
            <div className="content-comment">Phim hay</div>
          </div>
          <div className="like">
            <img src={likeImg} alt="..." />
            <span className="likes">0 thch</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RateOfFilmDetail;
